import React from 'react';
import EntityForm from 'modules/entity/forms';
import { Field } from 'formik';
import { KntInputField } from 'shared/components/Fields';
import { Button } from '@material-ui/core';
import { useAppState } from 'shared/state';
import { useNotistack } from 'shared/hooks';
import { Loader } from 'shared/components';

type Props = {
	newsValues: any;
	onSuccess: () => void;
};

export const NewsTemplateModal = ({ newsValues, onSuccess }: Props) => {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();

	console.log('newsValues', newsValues);

	return (
		<EntityForm.Main
			entity="templates"
			name="AllTemplates"
			url={`/${companyId}/notification-templates/`}
			sendAsFormData={false}
			method="post"
			fields={[
				{
					name: 'subject',
					required: true,
					onSubmitKey: ''
				},
				{
					name: 'title',
					required: true,
					value: newsValues.subject,
					onSubmitKey: ''
				},
				{
					name: 'body',
					required: true,
					value: newsValues.body,
					onSubmitKey: ''
				}
			]}
			onSuccess={() => {
				onSuccess();
			}}
			onError={(error, resetForm): any => {
				const errorText = error?.response?.data?.message || 'Something went wrong';
				showNotification({ message: errorText, variant: 'error' });
			}}
		>
			{({ values, isSubmitting }) => {
				return (
					<div className="case-form">
						<div className="case-form__content">
							<div className="case-form__input-row mb-15">
								<div className="case-form__input-row-label text-base font-bold">Template name</div>
								<Field
									component={KntInputField}
									type="text"
									placeholder="Template name here"
									name="subject"
									autoComplete="off"
								/>
							</div>
						</div>
						<div className="case-form__submit-row flex-center-end">
							<Button
								variant="contained"
								disabled={isSubmitting}
								onClick={() => {
									onSuccess();
								}}
							>
								Cancel
							</Button>
							<Button
								className="!ml-5"
								variant="contained"
								color="primary"
								disabled={isSubmitting || !values.subject}
								type="submit"
							>
								Create
							</Button>
						</div>
						{isSubmitting ? <Loader tiled withOverlay /> : null}
					</div>
				);
			}}
		</EntityForm.Main>
	);
};
