import React from 'react';
import Grid from '@material-ui/core/Grid';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import { AsyncSelect } from 'shared/components';
import get from 'lodash/get';
import { KntTwilioStyle } from 'shared/services';

type Props = {
	newsType: string;
	handleChange: any;
	values: any;
	companyId: number | undefined;
	setFieldValue: any;
	isUpdate: boolean;
};
export const NewsTypesElements = (props: Props) => {
	const { newsType, handleChange, values, companyId, setFieldValue, isUpdate = null } = props;
	return (
		<>
			{!isUpdate && (
				<Grid container spacing={3}>
					<Grid item xs={12}>
						<RadioGroup
							aria-label="News type"
							name="news-type"
							value={newsType}
							onChange={handleChange}
							className="news-types flex !flex-row items-center"
						>
							<FormControlLabel value="staff" control={<Radio color="primary" />} label="Staff" />
							<FormControlLabel
								value="client"
								control={<Radio color="primary" />}
								label="Clients"
							/>
						</RadioGroup>
					</Grid>
				</Grid>
			)}

			{newsType === 'staff' && (
				<Grid container spacing={3}>
					<Grid item xs={12} sm={4}>
						<div className="form-page__row mb-5">
							<div className="form-page__label text-md mb-2.5 font-bold">Department</div>
							{/* @ts-ignore */}
							<AsyncSelect
								name="route"
								customStylesProps={KntTwilioStyle}
								label=""
								placeholder="Select Department Name"
								optionLabel="name"
								optionValue="id"
								loadOptionsUrl={`/${companyId}/departments/`}
								loadOptionsParams={(search: string) => ({
									extra: {
										search,
										is_staff: 'true'
									}
								})}
								value={get(values, 'staff_departments')}
								loadOptionsKey="results"
								isMulti
								isSearchable={true}
								isClearable={true}
								onChange={(option: any) => {
									setFieldValue('staff_departments', option);
								}}
							/>
						</div>
					</Grid>
				</Grid>
			)}
			{newsType === 'client' && (
				<Grid container spacing={3}>
					<Grid item xs={12} sm={4}>
						<div className="form-page__row mb-5">
							<div className="form-page__label text-md mb-2.5 font-bold">
								Client groups (Optional)
							</div>
							{/* @ts-ignore */}
							<AsyncSelect
								name="route"
								customStylesProps={KntTwilioStyle}
								label=""
								placeholder="Client groups here"
								optionLabel="name"
								optionValue="id"
								loadOptionsUrl={`/${companyId}/categories/`}
								loadOptionsParams={(search: string) => ({
									extra: {
										search
									}
								})}
								value={get(values, 'categories')}
								loadOptionsKey="results"
								isMulti
								isSearchable={true}
								isClearable={true}
								onChange={(option: any) => {
									setFieldValue('categories', option);
								}}
							/>
						</div>
					</Grid>
				</Grid>
			)}
		</>
	);
};
