import React, { useState } from 'react';
import { Avatar, Fab } from '@material-ui/core';
import MicRoundedIcon from '@material-ui/icons/MicRounded';
import StopIcon from '@material-ui/icons/Stop';
import ClearIcon from '@material-ui/icons/Clear';
import { default as dayjs } from 'dayjs';
import { VoiceRecorder } from 'shared/components';
import { useUploadAudio } from '../hooks';
import { useNewsContext } from '../context/NewsContext';

// eslint-disable-next-line @typescript-eslint/no-var-requires
const utc = require('dayjs/plugin/utc');
dayjs.extend(utc);

function RecorderForm(props: any) {
	const [command, setCommand] = useState<any>(null);
	const [time, setTime] = useState<any>(0);
	const [timerId, setTimerId] = useState<any>(0);
	const [cancel, setCancel] = useState<any>(false);
	const { uploadAudioFile } = useUploadAudio();
	const { audioUploadProgress } = useNewsContext();

	const handleStartRecordAudio = () => {
		const timerId = setInterval(() => {
			setTime((time: any) => time + 1000);
		}, 1000);

		setTimerId(timerId);
		setCommand('start');
		setTime(0);
	};

	const handleStopRecordAudio = () => {
		clearInterval(timerId);
		setCancel(false);
		setCommand('stop');
	};

	const handleCancelRecord = () => {
		setCancel(true);
		clearInterval(timerId);
		setCommand('stop');
	};

	const handleOnStopRecording = (blob: any) => {
		if (!cancel) {
			void uploadAudioFile(blob);
		}
		setCommand(null);
	};

	return (
		<div className="recorder-box flex items-center">
			{command === 'start' && (
				<div className="recorder-box__counter mr-5">
					<div className="w-100 mr-4 flex">
						<div className="knx-recording-pulse" />
						<Avatar
							className="rct-notify mr-2.5 rounded-full !bg-kred-pink"
							style={{
								width: '20px',
								height: '20px'
							}}
						>
							{' '}
						</Avatar>
						{/*@ts-ignore*/}
						<div className="knx-recording-text">{dayjs(time).utc().format('HH:mm:ss')}</div>
					</div>
				</div>
			)}
			<div className="recorder-box__actions">
				{command === 'start' && (
					<Fab
						color="primary"
						// aria-label="file-attach"
						size="small"
						className="!mr-2"
						onClick={handleCancelRecord}
					>
						<ClearIcon />
					</Fab>
				)}
				{command === 'start' && (
					<Fab
						color="primary"
						aria-label="voice-message"
						size="small"
						onClick={handleStopRecordAudio}
					>
						<StopIcon />
					</Fab>
				)}
				{!command && (
					<div className="case-chat-btn-wr mic-scroll">
						<Fab
							color="primary"
							aria-label="voice-message"
							size="small"
							disabled={!!audioUploadProgress}
							onClick={handleStartRecordAudio}
						>
							<MicRoundedIcon />
						</Fab>
					</div>
				)}
			</div>
			<VoiceRecorder command={command} onStop={handleOnStopRecording} />
		</div>
	);
}

export default RecorderForm;
