/*
DISCLAIMER: THIS COMPONENT ALMOST IS NOT TYPED. BECAUSE
IT WAS COPIED FROM ACTUALLY TESTED AND WORKING OLD VERSION.
 I ALSO HAD INSUFFICIENT TIME TO MEET DEADLINES
 */
import React, { useEffect, useRef, useState } from 'react';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { ErrorMessage, Field } from 'formik';
import QuillEditorNews from 'shared/components/Fields/QuillEditorNews';
import { Link } from 'react-router-dom';
import { AsyncSelect, Loader, SupRequired } from 'shared/components';
import get from 'lodash/get';
import RecorderForm from './RecorderForm';
import UploadBox from './UploadBox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import DayJsUtils from '@date-io/dayjs';
import Checkbox from '@material-ui/core/Checkbox';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import { makeStyles } from '@material-ui/core/styles';
import { nanoid } from 'nanoid';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import { NewsTypesElements } from './NewsTypeElements';
import dayjs from 'dayjs';
import { caseFilterStyle, KntTwilioStyle } from 'shared/services';
import { useNewsContext } from '../context/NewsContext';
import { useAppState } from 'shared/state';
import { WhiteModal } from 'shared/components/Elements/WhiteModal';
import { NewsTemplateModal } from './CreateTemplatePopup';
import { KntInputField, SelectField } from '../../../shared/components/Fields';
import isNull from 'lodash/isNull';

const useStyles = makeStyles((theme) => ({
	formControl: {
		width: '100%'
	},
	selectEmpty: {
		marginTop: theme.spacing(2)
	},
	pullLeft: {
		// margin: "-10px"
	}
}));

type Props = {
	isSubmitting: boolean;
	values: any;
	setFieldValue: any;
	keywords: any;
	frequencies: any;
	statuses: any;
	campaignId: number;
	isUpdate?: any;
	setFieldError: any;
	errors: any;
};

const NewsFormElements = ({
	isSubmitting,
	values,
	setFieldValue,
	keywords,
	frequencies,
	statuses,
	campaignId,
	setFieldError,
	errors,
	isUpdate = false
}: Props) => {
	const classes = useStyles();
	const { audioResponse, setAudioResponse } = useNewsContext();

	const selectFrequency = (frequency: any) => {
		switch (frequency) {
			case 'ONE_TIME':
				return 'DAILY';
			case 'DAILY':
			case 'WEEKLY':
			case 'MONTHLY':
			case 'YEARLY':
			case 'QUARTERLY': {
				return frequency;
			}
			case 'EVENTS': {
				return 'DAILY';
			}
			default:
				return 'DAILY';
		}
	};

	const prepareDates = (dates: any) =>
		(dates as any).reduce(
			(acc: any, curr: any) => [...acc, { ...curr, date: new Date(curr.time) }],
			[]
		);
	const removeGTMFromString = (string: string) => string?.slice(0, -1);

	const uuid = nanoid();
	const [templateOption, setTemplateOption] = useState('');
	const [templateModal, showTemplateModal] = useState(false);
	const [uploadMeta, setUploadMeta] = useState(null);
	const [cancelSending, setCancelSending] = useState(false);
	const [files, setFiles] = useState(values.files);
	const [sendTime, setSendTime] = useState('schedule');
	const [frequency, setFrequency] = useState(
		selectFrequency(isUpdate ? selectFrequency(isUpdate.frequency) : 'DAILY')
	);
	const [msgFrequency, setMsgFrequency] = useState(values.frequency);
	const [selectedDate, handleDateChange] = useState<any>(
		isUpdate ? new Date(removeGTMFromString(isUpdate?.start_time)) : new Date(values.start_time)
	);

	const [selectedDateRepeat, handleDateChangeRepeat] = useState<any>({
		start: new Date(isUpdate ? removeGTMFromString(isUpdate?.start_time) : values.start_time),
		//we expect end_time as null when Forever option is selected while creating news
		end:
			isUpdate && !isNull(isUpdate?.end_time)
				? new Date(isUpdate ? removeGTMFromString(isUpdate?.end_time) : values.end_time)
				: null
	});

	const [selectedDateMulti, handleDateChangeMulti] = useState(
		isUpdate.frequency === 'EVENTS'
			? prepareDates(isUpdate.event_times)
			: [{ id: uuid, date: new Date() }]
	);

	const [state, setState] = React.useState<any>({
		startNow: false,
		forever: isUpdate ? isNull(isUpdate.end_time) : false,
		staffOnly: false,
		newsType: isUpdate ? values.type : 'staff',
		isSubmitting: false,
		open_for_cases: isUpdate ? values.open_for_cases : false
	});
	const { companyId } = useAppState();
	const isInitialMount = useRef(true);

	const { staffOnly, newsType } = state;

	const handleCheckChange = (event: any) => {
		setState({ ...state, [event.target.name]: event.target.checked });
	};

	useEffect(() => {
		setFieldValue('open_for_cases', state.open_for_cases);
	}, [state.open_for_cases]);

	useEffect(() => {
		if (isOnlyHTML(values.body)) {
			setFieldError('body', 'Required');
			setFieldValue('body', '');
		}
	}, [values.body]);

	useEffect(() => {
		if (audioResponse) {
			setFiles((prevState: any) => [...prevState, audioResponse]);
		}
	}, [audioResponse]);

	useEffect(() => {
		return () => {
			setFiles([]);
			setAudioResponse(null);
		};
	}, []);

	useEffect(() => {
		setFieldValue('files', files);
	}, [files]);

	useEffect(() => {
		if (sendTime === 'now') {
			// setFieldValue('start_time', new Date());
			// setFieldValue('end_time', new Date());

			setFieldValue('start_time', 'now');
			setFieldValue('end_time', 'now');

			setFieldValue('frequency', 'ONE_TIME');
		} else {
			setFieldValue('start_time', selectedDate);
			setFieldValue('frequency', msgFrequency);
		}
	}, [sendTime]);

	const setTime = (stateField: any, field: any, value: any) => {
		if (state[stateField]) {
			if (state.startNow) {
				// setFieldValue('start_time', new Date());
				setFieldValue(field, 'now');
			}

			setFieldValue('end_time', null);
		} else {
			setFieldValue(field, value);
		}
	};

	useEffect(() => {
		(() => {
			switch (msgFrequency) {
				case 'ONE_TIME': {
					setFieldValue('frequency', 'ONE_TIME');
					break;
				}
				case 'REPEAT': {
					setFieldValue('frequency', frequency);
					setTime('startNow', 'start_time', selectedDateRepeat.start);
					setTime('forever', 'end_time', selectedDateRepeat.end);
					break;
				}
				case 'EVENTS': {
					setFieldValue('frequency', 'EVENTS');
					break;
				}
				default:
					return null;
			}
		})();
	}, [msgFrequency, frequency]);

	//One time
	useEffect(() => {
		setFieldValue('start_time', selectedDate);
		setFieldValue('end_time', selectedDate);
	}, [selectedDate]);

	useEffect(() => {
		setTime('startNow', 'start_time', selectedDateRepeat.start);
		setTime('forever', 'end_time', selectedDateRepeat.end);
	}, [selectedDateRepeat]);

	useEffect(() => {
		setTime('startNow', 'start_time', selectedDateRepeat.start);
		setTime('forever', 'end_time', selectedDateRepeat.end);
	}, [state]);

	useEffect(() => {
		const times = selectedDateMulti.map((dateItem: any) => ({
			time: dayjs(dateItem.date).format('YYYY-MM-DDTHH:mm:ss')
		}));
		setFieldValue('event_times', times);
	}, [selectedDateMulti]);

	useEffect(() => {
		if (staffOnly) {
			setFieldValue('company_ids', undefined);
		}
	}, [staffOnly]);

	useEffect(() => {
		if (isInitialMount.current) {
			isInitialMount.current = false;
		} else {
			// Your useEffect code here to be run on update
			if ('subject' in errors && state.isSubmitting) {
				scrollIntoNotFilledField('subject');
			}
			if ('body' in errors && !('subject' in errors) && state.isSubmitting) {
				scrollIntoNotFilledField('body');
			}
		}
	}, [errors, state.isSubmitting]);

	useEffect(() => {
		if (state.isSubmitting) {
			setTimeout(() => {
				setState((prev: any) => ({ ...prev, isSubmitting: false }));
			}, 100);
		}
	}, [state.isSubmitting]);

	const handleChange = ({ target: { value, name } }: any) => {
		// setSendTime(value);

		switch (name) {
			case 'sendTime': {
				setSendTime(value);
				break;
			}
			case 'delivFrequency': {
				setMsgFrequency(value);
				break;
			}
			case 'frequency': {
				setFrequency(value);
				break;
			}
			case 'news-type': {
				setState((prev: any) => ({ ...prev, newsType: value }));
				setFieldValue('type', value);
				break;
			}
			default:
				return null;
		}
	};

	const handleDateMulti = (date: any, id: any) => {
		const selectedDate = selectedDateMulti.find((date: any) => date.id === id);
		const filteredDates = selectedDateMulti.filter((date: any) => date.id !== id);
		selectedDate.date = date;
		const allDate = [...filteredDates, selectedDate];
		handleDateChangeMulti(allDate);
	};

	const handleMultiDateDelete = (id: any) => {
		const filteredDates = selectedDateMulti.filter((date: any) => date.id !== id);
		handleDateChangeMulti(filteredDates);
	};

	const handleIsSubmitting = () => {
		setTimeout(() => {
			scrollIntoNotFilledField('subject');
		}, 0);
		setState((prev: any) => ({ ...prev, isSubmitting: true }));
	};

	function scrollIntoNotFilledField(field: string) {
		const title = document.querySelector(`#subject`);
		const body = document.querySelector(`#body`);
		if (field === 'subject') title?.scrollIntoView({ block: 'start', behavior: 'smooth' });
		if (field === 'body') body?.scrollIntoView({ block: 'start', behavior: 'smooth' });
	}

	if (process.env.NODE_ENV === 'development') {
		console.log('newsFormElements state', { state, sendTime });
	}

	const toggleTemplatePopup = () => showTemplateModal((prev) => !prev);

	return (
		<Paper className="uni-page__paper">
			<MuiPickersUtilsProvider utils={DayJsUtils}>
				<div className="form-page react-select-dropdown p-2 sm:p-6">
					{/*News Type Select radio buttons*/}
					{!campaignId && (
						<NewsTypesElements
							newsType={newsType}
							handleChange={handleChange}
							values={values}
							companyId={companyId}
							setFieldValue={setFieldValue}
							isUpdate={isUpdate}
						/>
					)}
					{!isUpdate && (
						<>
							<Grid container spacing={3} alignItems="center">
								<Grid item xs={12} sm={8} md={6} lg={4}>
									<div className="react-select-dropdown">
										{/*@ts-ignore*/}
										<AsyncSelect
											name="route"
											customStylesProps={KntTwilioStyle}
											label=""
											placeholder="Choose Template (Optional)"
											optionLabel="subject"
											optionValue="id"
											loadOptionsUrl={`/${companyId}/notification-templates/`}
											loadOptionsParams={(search: string) => ({
												extra: {
													search
												}
											})}
											value={templateOption}
											loadOptionsKey="results"
											isSearchable={true}
											isClearable={true}
											onChange={(option: any) => {
												setTemplateOption(option);
												if (option) {
													setFieldValue('subject', option.title);
													setFieldValue('body', option.body);
												} else {
													setFieldValue('subject', ' ');
													setFieldValue('body', ' ');
												}
											}}
										/>
									</div>
								</Grid>
								<Grid item xs={12} sm={4} md={4}>
									<Button
										variant="contained"
										color="primary"
										disabled={isSubmitting}
										component={Link}
										className="!text-white"
										to={`/news/create-template`}
									>
										New Template
									</Button>
								</Grid>
							</Grid>
						</>
					)}

					<Grid container spacing={3}>
						<Grid item xs={12} sm={8} md={6} lg={4}>
							<div className="form-page__row mb-5" id={'subject'}>
								<div className="form-page__label text-md mb-2.5 font-bold">
									News title
									<SupRequired className="text-kred-pink" />
								</div>
								<Field
									component={KntInputField}
									type="text"
									// error
									// helperText="Incorrect entry."
									// label="Template title"
									placeholder="News title here"
									name="subject"
									variant="outlined"
									size="small"
									fullWidth
									autoComplete="off"
								/>
							</div>
						</Grid>
					</Grid>
					<div className="form-page__row mb-5" id={'body'}>
						<div className="form-page__label text-md mb-2.5 font-bold">
							Description
							<SupRequired className="text-kred-pink" />
						</div>
						<Field
							component={QuillEditorNews}
							type="text"
							name="body"
							bounds={'.form-page'}
							{...{ keywords, option: templateOption }}
						/>
						<ErrorMessage name="body">
							{(message) => (
								<div className="MuiFormHelperText-root MuiFormHelperText-contained Mui-error MuiFormHelperText-marginDense">
									{message}
								</div>
							)}
						</ErrorMessage>
					</div>
					<Grid container spacing={3}>
						<Grid item xs={12} sm={9}>
							<div className="form-page__row mb-5">
								<div className="form-page__label text-md mb-2.5 font-bold">File Upload</div>
								<UploadBox
									uploadMeta={uploadMeta}
									setUploadMeta={setUploadMeta}
									cancelSending={cancelSending}
									setCancelSending={setCancelSending}
									files={files}
									setFiles={setFiles}
								/>
							</div>
						</Grid>
						<Grid item xs={12} sm={8} md={6} lg={4}>
							<div className="form-page__row mb-5">
								<div className="form-page__label text-md mb-2.5 font-bold">Record Audio</div>
								<RecorderForm />
							</div>
							<div className="form-page__row custom-date-picker mb-5">
								<div className="form-page__label mb-2 font-bold">Sending status</div>
								<Field
									component={SelectField}
									className="basic-single"
									classNamePrefix="select"
									isClearable
									isSearchable
									name="status"
									options={statuses}
									placeholder="Select sending status"
									styles={caseFilterStyle}
								/>
							</div>
							<div className="form-page__row mb-5">
								<FormControlLabel
									control={
										<Checkbox
											checked={state.open_for_cases}
											onChange={handleCheckChange}
											name="open_for_cases"
											color="primary"
										/>
									}
									label="Allow Case Creation"
									classes={{ label: 'whitespace-nowrap' }}
								/>
							</div>
							{values.open_for_cases && (
								<div className="form-page__row react-select-dropdown mb-5">
									<div className="form-page__label text-md mb-2.5 font-bold ">Department</div>
									<div className="react-select-dropdown">
										{/*@ts-ignore*/}
										<AsyncSelect
											name="route"
											label=""
											customStylesProps={KntTwilioStyle}
											placeholder="Select Department Name"
											optionLabel="name"
											optionValue="id"
											loadOptionsUrl={`/${companyId}/departments/`}
											loadOptionsParams={(search: string) => ({
												extra: {
													search,
													is_staff: newsType === 'staff' ? 'true' : 'false'
												}
											})}
											value={get(values, 'department')}
											loadOptionsKey="results"
											isSearchable={true}
											isClearable={true}
											customStyles="KntTwilioStyle"
											onChange={(option: any) => {
												setFieldValue('department', option);
											}}
										/>
									</div>
								</div>
							)}
							{values.department && (
								<div className="form-page__row mb-5">
									<div className="form-page__label text-md mb-2.5 font-bold ">Sub department</div>
									<div className="react-select-dropdown">
										{/*@ts-ignore*/}
										<AsyncSelect
											name="route"
											customStylesProps={KntTwilioStyle}
											// label="Change Priority"
											label=""
											placeholder="Select Sub Department Name"
											optionLabel="name"
											optionValue="id"
											loadOptionsUrl={`/${companyId}/departments/${get(
												values,
												'department.id'
											)}/sub_departments/`}
											loadOptionsParams={(search: string) => ({
												extra: {
													search
												}
											})}
											value={get(values, 'sub_department')}
											loadOptionsKey="results"
											isSearchable={true}
											isClearable={true}
											onChange={(option: any) => {
												setFieldValue('sub_department', option);
											}}
										/>
									</div>
								</div>
							)}
							{(() => {
								while (get(values, 'sub_department.sub_departments_count') > 0) {
									return (
										<div className="form-page__row mb-5">
											<div className="form-page__label text-md mb-2.5 font-bold ">
												Select sub department of sub deparment
											</div>
											<div className="react-select-dropdown">
												{/*@ts-ignore*/}
												<AsyncSelect
													name="route"
													customStyles="KntTwilioStyle"
													label=""
													placeholder="Select Sub Department Name"
													optionLabel="name"
													optionValue="id"
													loadOptionsUrl={`/${companyId}/departments/${get(
														values,
														'sub_department.id'
													)}/sub_departments/`}
													loadOptionsParams={(search: string) => ({
														extra: {
															search
														}
													})}
													value={get(values, 'sub_department')}
													loadOptionsKey="results"
													isSearchable={true}
													isClearable={true}
													onChange={(option: any) => {
														setFieldValue('sub_department', option);
													}}
												/>
											</div>
										</div>
									);
								}
							})()}
							<div className="form-page__row mb-5">
								<RadioGroup
									aria-label="time"
									name="sendTime"
									value={sendTime}
									onChange={handleChange}
								>
									<div className="flex">
										<FormControlLabel
											value="now"
											control={<Radio color="primary" />}
											label="Send now"
										/>
										<FormControlLabel
											value="schedule"
											control={<Radio color="primary" />}
											label="Schedule"
										/>
									</div>
								</RadioGroup>
							</div>
							{sendTime === 'schedule' && (
								<>
									<div className="form-page__row mb-5">
										<div className="form-page__label text-md mb-2.5 font-bold">
											Delivery Frequency
										</div>
										<RadioGroup
											aria-label="frequency"
											name="delivFrequency"
											value={msgFrequency}
											onChange={handleChange}
										>
											<div className="flex">
												<FormControlLabel
													value="ONE_TIME"
													control={<Radio color="primary" />}
													label="One time"
												/>
												<FormControlLabel
													value="REPEAT"
													control={<Radio color="primary" />}
													label="Repeat"
												/>
												<FormControlLabel
													value="EVENTS"
													control={<Radio color="primary" />}
													label="Multiple Events"
												/>
											</div>
										</RadioGroup>
									</div>
									{/* One time option */}
									{msgFrequency === 'ONE_TIME' && (
										<div className="form-page__row custom-date-picker mb-5">
											<DateTimePicker
												label="Send time"
												size="small"
												fullWidth
												inputVariant="outlined"
												value={selectedDate}
												onChange={handleDateChange}
											/>
										</div>
									)}
									{/* Repeat option */}
									{msgFrequency === 'REPEAT' && (
										<>
											<div className="form-page__row custom-date-picker mb-5">
												<FormControl variant="outlined" className={classes.formControl}>
													<TextField
														id="standard-select-currency"
														select
														name="frequency"
														value={frequency}
														label="Select Frequency"
														variant="outlined"
														size="small"
														onChange={handleChange}
													>
														{frequencies.map((menu: any) => (
															<MenuItem value={menu.code} key={menu.code}>
																{menu.name}
															</MenuItem>
														))}
													</TextField>
												</FormControl>
											</div>

											<div className="form-page__row custom-date-picker mb-5">
												<Grid container item xs={12} spacing={3} alignItems="center">
													{!state.startNow && (
														<Grid item xs={12} sm={8}>
															<DateTimePicker
																label="Start time"
																inputVariant="outlined"
																value={selectedDateRepeat.start}
																size="small"
																fullWidth
																onChange={(date) =>
																	handleDateChangeRepeat((prevState: any) => ({
																		...prevState,
																		start: date
																	}))
																}
															/>
														</Grid>
													)}

													<Grid item xs={12} sm={4}>
														<FormControlLabel
															control={
																<Checkbox
																	checked={state.startNow}
																	onChange={handleCheckChange}
																	name="startNow"
																	color="primary"
																/>
															}
															label="Start now"
															classes={{ label: 'whitespace-nowrap' }}
														/>
													</Grid>
												</Grid>
											</div>
											<div className="form-page__row custom-date-picker mb-5">
												<Grid container item xs={12} spacing={3} alignItems="center">
													{!state.forever && (
														<Grid item xs={12} sm={8}>
															<DateTimePicker
																label="End time"
																inputVariant="outlined"
																size="small"
																fullWidth
																value={selectedDateRepeat.end}
																onChange={(date) =>
																	handleDateChangeRepeat((prevState: any) => ({
																		...prevState,
																		end: date
																	}))
																}
															/>
														</Grid>
													)}
													<Grid item xs={12} sm={4}>
														<FormControlLabel
															control={
																<Checkbox
																	checked={state.forever}
																	onChange={handleCheckChange}
																	name="forever"
																	color="primary"
																/>
															}
															label="Forever"
														/>
													</Grid>
												</Grid>
											</div>
										</>
									)}
									{/* Multiple Events */}
									{msgFrequency === 'EVENTS' && (
										<div className="form-page__row mb-5">
											<div className="mb-5">
												{selectedDateMulti.map((dateItem: any) => (
													<div className="form-page__row custom-date-picker mb-5" key={dateItem.id}>
														<Grid container item xs={12} spacing={1} alignItems="center">
															<Grid item xs={10} sm={8}>
																<DateTimePicker
																	label="Start time"
																	inputVariant="outlined"
																	size="small"
																	fullWidth
																	value={dateItem.date}
																	onChange={(date) => {
																		handleDateMulti(date, dateItem.id);
																	}}
																/>
															</Grid>
															<Grid item xs={2} sm={4}>
																<IconButton
																	aria-label="delete"
																	className={classes.pullLeft}
																	onClick={() => handleMultiDateDelete(dateItem.id)}
																>
																	<DeleteIcon />
																</IconButton>
															</Grid>
														</Grid>
													</div>
												))}
											</div>
											<Button
												variant="outlined"
												color="primary"
												onClick={() => {
													handleDateChangeMulti((prevState: any) => [
														...prevState,
														{ id: uuid, date: new Date() }
													]);
												}}
											>
												Add one more
											</Button>
										</div>
									)}
									<div className="form-page__row mb-5">
										<div className="form-page__label text-md mb-2.5 font-bold">Timezone</div>
										<div className="react-select-dropdown">
											{/*@ts-ignore*/}
											<AsyncSelect
												name="route"
												customStylesProps={KntTwilioStyle}
												label=""
												placeholder="Choose Timezones here"
												optionLabel="title"
												optionValue="id"
												loadOptionsUrl="/timezones/"
												loadOptionsParams={(search: string) => ({
													extra: {
														search
													}
												})}
												value={get(values, 'timezone')}
												loadOptionsKey="results"
												isSearchable={true}
												isClearable={true}
												customStyles="KntTwilioStyle"
												onChange={(option: any) => {
													setFieldValue('timezone', option);
												}}
											/>
										</div>
									</div>
								</>
							)}
						</Grid>
					</Grid>

					<div className="form-page__row mb-5">
						<Button
							variant="contained"
							disabled={isSubmitting}
							component={Link}
							className="!mr-5"
							to={campaignId ? `/news/campaigns/view/${campaignId}` : '/news/news-list'}
						>
							Cancel
						</Button>
						<Button
							className="ml-20"
							variant="contained"
							color="primary"
							disabled={isSubmitting /*|| (newsType === 'client' && !values.categories)*/}
							type="submit"
							onClick={handleIsSubmitting}
						>
							{`${isUpdate ? 'Update' : 'Create'}`}
						</Button>
						{isSubmitting ? <Loader tiled withOverlay /> : null}
						{!templateOption && values.subject && values.body && !isUpdate && (
							<Button
								className="!ml-5"
								variant="contained"
								color="primary"
								disabled={isSubmitting}
								onClick={toggleTemplatePopup}
							>
								Save as template
							</Button>
						)}
					</div>
					<WhiteModal handleModalOpen={toggleTemplatePopup} open={!!templateModal}>
						<NewsTemplateModal newsValues={values} onSuccess={toggleTemplatePopup} />
					</WhiteModal>
				</div>
			</MuiPickersUtilsProvider>
		</Paper>
	);
};

export default NewsFormElements;

function isOnlyHTML(testMe: any) {
	const testMeEl = document.createElement('div');
	testMeEl.innerHTML = testMe; // browser does the parsing
	return hasNoTextChildren(testMeEl);
}

// recursively check for text elements
function hasNoTextChildren(element: any) {
	for (let i = 0; i < element.childNodes.length; i++) {
		const child = element.childNodes[i];
		if (child instanceof Text) {
			return false;
		} else if (hasNoTextChildren(child) === false) {
			return false;
		}
	}
	return true;
}
