import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Button from '@material-ui/core/Button';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import Upload from 'rc-upload';
import { ErrorBoundary } from 'shared/components';

import { useAppState } from 'shared/state';
import config from 'config';
import { useAuth } from 'modules/auth/hooks/useAuth';

const CancelToken = axios.CancelToken;
let cancelAx: any;
let currFile: any;

function FileUploader({
	uploadMeta,
	setUploadMeta,
	cancelSending,
	setCancelSending,
	setFiles
}: any) {
	const { companyId } = useAppState();
	const { token } = useAuth();
	const [progress, setProgress] = useState<any>(null);

	useEffect(() => {
		setUploadMeta(progress);
	}, [progress]);

	useEffect(() => {
		if (cancelSending) {
			const errorMessage = 'Operation canceled by the user.';
			cancelAx(errorMessage);
		}
	}, [cancelSending]);

	const fileOnStart = (file: any) => {
		const reader = new FileReader();
		const imgFile = file;

		reader.readAsDataURL(imgFile);

		currFile = {
			...file,
			uid: file.uid,
			type: file.type,
			name: file.name,
			key: file.key,
			size: file.size,
			lastModified: file.lastModified
		};
	};

	const fileOnSuccess = (response: any, _file: any, currFile: any) => {
		// console.log("response", response);
		setProgress(null);
		setFiles((prevState: any) => [...prevState, response]);
	};

	const onProgress = ({ percent }: any) => {
		setProgress(Number(percent));
	};

	const onError = (err: any) => {
		setCancelSending(false);
		setProgress(null);
	};

	const action = `${config.API_ROOT}/${companyId}/upload/`;

	return (
		<div>
			{!uploadMeta ? (
				<Upload
					//@ts-ignore
					beforeUpload={fileOnStart}
					customRequest={({
						file,
						filename,
						headers = {
							Authorization: 'Token ' + token
						}
					}) => {
						const formData = new FormData();
						formData.append(filename as string, file);
						axios
							.post(action, formData, {
								headers,
								onUploadProgress: ({ total, loaded }) => {
									//@ts-ignore
									onProgress({ percent: Math.round((loaded / total) * 100).toFixed(2) }, file);
								},
								cancelToken: new CancelToken((c) => {
									cancelAx = c;
								})
							})
							.then(({ data: response }) => {
								fileOnSuccess(response, file, currFile);
							})
							.catch((err) => {
								onError(err);
							});

						return {
							abort() {
								console.log('upload progress is aborted.');
							}
						};
					}}
				>
					<Button
						variant="outlined"
						className="pending-upload__btn"
						color="default"
						disabled={!!uploadMeta}
						endIcon={<CloudUploadIcon />}
					>
						Upload
					</Button>
				</Upload>
			) : (
				<Button
					variant="outlined"
					className="pending-upload__btn"
					color="default"
					disabled={!!uploadMeta}
					endIcon={<CloudUploadIcon />}
				>
					Upload
				</Button>
			)}
		</div>
	);
}

export default function FileUploaderWithErrorBoundary(props: any) {
	return (
		<ErrorBoundary>
			<FileUploader {...props} />
		</ErrorBoundary>
	);
}
