import React from 'react';
import get from 'lodash/get';
import FileUploader from './FileUploader';
import config from 'config';
import { CircularProgress } from '@material-ui/core';
import { useNewsContext } from '../context/NewsContext';

const imgPath = `${config.PUBLIC_ASSETS}img/`;

type Props = {
	uploadMeta: number | null;
	setUploadMeta: any;
	cancelSending: boolean;
	setCancelSending: any;
	files: any;
	setFiles: any;
};

const getFormat = (filename: string) => filename.split('.').pop();

function UploadBox({
	uploadMeta,
	setUploadMeta,
	cancelSending,
	setCancelSending,
	files,
	setFiles
}: Props) {
	const removeFile = (id: number) => {
		const filteredFiles = files.filter((file: any) => file.id !== id);
		setFiles(filteredFiles);
	};

	const { audioUploadProgress, setAudioCancelSending } = useNewsContext();

	const uploadCancelHandler = () => {
		if (audioUploadProgress) {
			setAudioCancelSending(true);
		} else {
			setCancelSending(true);
			setUploadMeta(null);
		}
	};

	return (
		<div className="uploadbox flex items-center">
			<div className="uploadbox__btn mr-5">
				<FileUploader
					{...{ uploadMeta, setUploadMeta, cancelSending, setCancelSending, files, setFiles }}
				/>
			</div>
			<div className="uploadbox__display">
				<div className="uploadbox__display-list  flex items-center">
					{files.map((file: any) => {
						if (file.thumbnail) {
							return (
								<div className="uploadbox__display-item mb-2.5" key={file.name}>
									<img src={get(file, 'thumbnail_150')} alt="" />
									<div
										className="uploadbox__display-item-remove"
										onClick={() => removeFile(file.id)}
									/>
								</div>
							);
						} else if (getFormat(file.name) === 'wav') {
							return (
								<div
									className="uploadbox__display-item uploadbox__display-item_audio mb-2.5"
									key={file.name}
								>
									<div
										className="uploadbox__display-item-remove"
										onClick={() => removeFile(file.id)}
									/>
									<audio controls>
										<source src={file && file.url} type="audio/wav" />
										<source src={file && file.url} type="audio/mpeg" />
										Your browser does not support the audio element.
									</audio>
								</div>
							);
						} else {
							return (
								<div className="uploadbox__display-item mb-2.5" key={file.name}>
									<div
										className="uploadbox__display-item-remove"
										onClick={() => removeFile(file.id)}
									/>
									<span className="inline-block text-xs leading-3">{file.name}</span>
								</div>
							);
						}
					})}
					<div className="uploadbox__display-item uploadbox__display-item_placeholder mb-2.5">
						<img src={`${imgPath}image-placeholder.png`} alt="" />
						{(uploadMeta !== null || audioUploadProgress !== null) && (
							<>
								<div className="upload-media-spinner">
									<CircularProgress
										variant="determinate"
										//@ts-ignore
										value={uploadMeta || audioUploadProgress}
									/>
								</div>
								<span className="upload-cancel-icon" onClick={uploadCancelHandler} />
							</>
						)}
					</div>
				</div>
			</div>
		</div>
	);
}

export default UploadBox;
